import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select } from 'antd';
import { classroom } from 'dump/data';

const { Option } = Select;
const { TextArea } = Input;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    groundworkTraining: '',
    building: '',
    floor: '',
    numberRoom: '',
    capacity: '',
    numberStudent: '',
    roomType: '',
    roomDevice: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormPointLadderFour extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = code => {
    const teacherMap = classroom.filter(teacher => teacher.code === code)[0];
    const initialValue = handleInitialValue(teacherMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Cp nht danh mc phng hc' : 'Thm danh mc phng hc'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="C s o to" {...formItemLayout}>
              {getFieldDecorator('groundworkTraining', {
                initialValue: initialValue.groundworkTraining,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="aaa">Cu Giy</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ta nh" hasFeedback>
              {getFieldDecorator('building', {
                initialValue: initialValue.building,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="aaa">A1</Option>
                  <Option value="bbb">A2</Option>
                  <Option value="ccc">A3</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item hasFeedback label="Tng" {...formItemLayout}>
              {getFieldDecorator('floor', {
                initialValue: initialValue.floor,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="aaa">1</Option>
                  <Option value="bbb">2</Option>
                  <Option value="ccc">3</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="S phng" hasFeedback>
              {getFieldDecorator('numberRoom', {
                initialValue: initialValue.numberRoom,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Sc cha" {...formItemLayout}>
              {getFieldDecorator('capacity', {
                initialValue: initialValue.capacity,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="S sv" hasFeedback>
              {getFieldDecorator('numberStudent', {
                initialValue: initialValue.numberStudent,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Loi phng" {...formItemLayout}>
              {getFieldDecorator('roomType', {
                initialValue: initialValue.roomType,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="aaa">Hi trng</Option>
                  <Option value="bbb">Phng lm vic</Option>
                  <Option value="ccc">Thc hnh</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Thit b phng" hasFeedback>
              {getFieldDecorator('roomDevice', {
                initialValue: initialValue.roomDevice,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<TextArea rows={4} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#/classroom/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPointLadderFour {...props} />
  </Page>
));



